<template>
  <el-container>
    <el-main>
      <el-table :data="userInfoList" align="center">
        <el-table-column prop="id" label="用户ID" />
        <el-table-column prop="userName" label="用户姓名" />
        <el-table-column prop="cardNum" label="证件号码" />
        <el-table-column prop="mobile" label="手机号" />
        <el-table-column prop="address" label="用户地区" />
        <el-table-column prop="policyNum" label="保单号/服务号" />
        <el-table-column prop="compony" label="所属企业" />
        <el-table-column prop="state" label="状态" />
        <el-table-column prop="server" label="当前服务" />
        <el-table-column label="操作" width='100'>
          <template slot-scope="scope">
            <el-button @click="handleClick()" type="text" size="small">查看</el-button>
            <el-button type="text" size="small" @click="disPath()">派单</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="demonstration-box">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="total, prev, pager, next"
          :total="1000">
        </el-pagination>
      </div>

      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>确认删除该条信息？</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
import { userManageInfo } from "../../../../data/userManage";
export default {
  name: "listTable",
  data() {
    return {
      userInfoList: Array(8).fill(userManageInfo),
      dialogVisible: false,
      currentPage: 1
    };
  },
  methods: {
    handleClick() {
      this.$router.push("userDetail?type=edit&id=66");
    },
    disPath() {
      this.$router.push("userDetail?type=disPath");
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }    
  }
};
</script>
<style lang="less" scoped>
</style>